import React, { useEffect } from 'react';
import {Container,Offcanvas,Navbar,Nav} from 'react-bootstrap';
export const NavbarP = () => {
    useEffect(() =>{
        let animado = document.querySelectorAll('.hidden');
        const Showscroll = () =>{
            let scrollTop = document.documentElement.scrollTop;
            for (let i = 0; i < animado.length; i++) {
                let alturaAnimado = animado[i].offsetTop;
                if(alturaAnimado - 500 < scrollTop){
                    animado[i].classList.remove('hidden');
                    animado[i].classList.add('animate__animated','animate__backInLeft')
                }
            }
        }

        window.addEventListener('scroll',Showscroll);
    },[])
    return (
        <Navbar className="text-primary scrollM" bg="Dark" expand={false} sticky="top">
            <Container fluid>
                <Navbar.Brand className="text-primary"> <i className="bi bi-code-slash"></i></Navbar.Brand>
                <Navbar.Toggle className="border-primary" aria-controls="offcanvasNavbar">
                    <i className="text-primary bi bi-list"></i>
                </Navbar.Toggle>
                <Navbar.Offcanvas id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel" placement="end">
                <Offcanvas.Header closeButton className='bg-Dark text-primary' closeVariant='white'>
                    <Offcanvas.Title id="offcanvasNavbarLabel">Menu</Offcanvas.Title>
                </Offcanvas.Header>
                <Offcanvas.Body className='bg-Dark text-primary'>
                    <Nav className="justify-content-end flex-grow-1 pe-3">
                        <Nav.Link className='textHover ps-1' href="#AboutMe">About Me</Nav.Link>
                        <Nav.Link className='textHover ps-1' href="#Skills">Skills</Nav.Link>
                        <Nav.Link className='textHover ps-1' href="#Projects">Projects</Nav.Link>
                        <Nav.Link className='textHover ps-1' href="#ContactMe">Contact</Nav.Link>
                    </Nav>
                </Offcanvas.Body>
            </Navbar.Offcanvas>
            </Container>
        </Navbar>
);
};
