import React, { useEffect } from 'react';

const handleMouseMove = (e) => {
        const el = document.querySelector(".wrapper");
        const d = el.getBoundingClientRect();
        let x = e.clientX - (d.left + Math.floor(d.width / 2));
        let y = e.clientY - (d.top + Math.floor(d.height / 2));
        x = x - x * 2;
        y = y - y * 2;
        document.documentElement.style.setProperty("--scale", 1.6);
        if(x<0){
            document.documentElement.style.setProperty("--x", 0 + "px");
        }
        document.documentElement.style.setProperty("--x", x / 2 + "px");
        document.documentElement.style.setProperty("--y", y / 2 + "px");
    };
    
const handleMouseLeave = () => {
    document.documentElement.style.setProperty("--scale", 1);
    document.documentElement.style.setProperty("--x", 0);
    document.documentElement.style.setProperty("--y", 0);
};
    let character = 0,
    word = 0,
    deleting = false,
    actualPhrase = [],
    end = false;

const loop =() => {
    const words = ["I'M ALEJANDRO","I'M WEB DEVELOPER"],
    $wordContainer = document.getElementById('wordChange');
    if(word < words.length){
        if(!deleting && character<=words[word].length){
            actualPhrase.push(words[word][character]);
            character++;
            $wordContainer.innerHTML = actualPhrase.join('');
        }
        if(deleting && character<= words[word].length){
            actualPhrase.pop(words[word][character]);
            character--;
            $wordContainer.innerHTML = actualPhrase.join('');
        }
        if(character === words[word].length){
            end = true;
            deleting = true;
        }
        if(deleting && character === 0){
            actualPhrase = []
            deleting = false
            word++;
            if(word === words.length){
                word = 0;
            }
        }
    }
    const spedUp = Math.random() * (80 -50) + 50;
    const normalSpeed = Math.random() * (300 -200) + 200;
    const time = end ? 500 : deleting ? spedUp : normalSpeed;
    setTimeout(loop, time)
}

export const  Background = () => {
    const codeS = "</>",
        codeE = "<>";
    useEffect(() => {
        loop();
    }, []);
    window.onscroll = function (){
        let scroll = document.documentElement.scrollTop
        if(scroll > 75){
            handleMouseLeave()
        }
    }
    return(
        <div className="wrapper bg-ID" onMouseMove={handleMouseMove} onClick={handleMouseLeave}>
            <div className='z-index-1000'>
                <h1 className="subtitle fw-bold">{codeE} <span id="wordChange"></span>| {codeS}</h1>
            </div>
            <img className='image'/>
        </div>
    )
}