import React, { useState } from 'react';
import { Form, Button } from "react-bootstrap";
import { Loader } from './Loader';
import { Message } from './Message';

const initialForm = {
    name: '',
    email: '',
    message: ''
}



export const ContactForm = () => {
    const [form,setForm] = useState(initialForm);
    const [error,setError] = useState({})
    const [loading,setloading] = useState(false)
    const [showMSG, setShowMSG] = useState(false)


    const handleChange = (e) =>{
        setForm({
            ...form,
            [e.target.name]: e.target.value
        })
    }
    const handleSubmit = (e) => {
        e.preventDefault()
        setError(validate(form))
        if(Object.keys(error).length === 0){
            setloading(true)
            fetch('https://formsubmit.co/ajax/ferralalejandro17@gmail.com',{
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({
                name: form.name,
                email: form.email,
                message: form.message
            })
            })
            .then(resp => resp.ok? resp.json() : Promise.reject(resp))
            .then(json =>{
                setloading(false)
                setShowMSG(true)
            })
            .catch(error => {});
        }
        handleReset();
        setTimeout(() => {
            setShowMSG(false)
        }, 3000);
    }

    const validate = (values) => {
        const errors = {}
        const regexName = /^[A-Za-zÑñÁáÉéÍíÓóÚúÜü\s]+$/
        const regexEmail = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()\.,;\s@\"]+\.{0,1})+([^<>()\.,;:\s@\"]{2,}|[\d\.]+))$/
        const regexComment = /^[A-Za-z0-9]{10,255}$/
        for (const key in values) {
            if(!values[key]){
                errors[key] = 'El campo es requerido'
            }
            if(key==='name'){
                if(!regexName.test(values[key])){
                    errors[key] = 'El nombre solo puede contener letras'
                }
            }
            
            if(key==='email'){
                if(!regexEmail.test(values[key])){
                    errors[key] = 'Correo no valido'
                }
            }
            if(key==='message'){
                if(!regexComment.test(values[key])){
                    errors[key] = 'El comentario debe tener minimo 10 caracteres y maximo 255'
                }
            }
        }
        return errors
    }

    const handleReset = (e) => {
        const $inputs = document.querySelectorAll('.formInput')
        $inputs.forEach(element => {
            element.value = '';
        });
        setForm(initialForm)
        setError({})
    }
    return(
        <>
        <Form className="text-Dark mt-3" onSubmit={handleSubmit} id='Form'>
            <Form.Group className="mb-3">
                <Form.Label>Name</Form.Label>
                <Form.Control className='formInput' name="name" id="name" type="text" placeholder="Enter name" onChange={handleChange}/>
                {/* <Form.Text className="text-muted"> We'll never share your email with anyone else. </Form.Text> */}
                {error.name && <span className='bg-secondary p-1 w-100 d-block text-center'>{error.name}</span>}
            </Form.Group>
            <Form.Group className="mb-3">
                <Form.Label>Email</Form.Label>
                <Form.Control className='formInput' name='email' id='email' type="email" placeholder="Enter email" onChange={handleChange}/>
                {error.email && <span className='bg-secondary p-1 w-100 d-block text-center'>{error.email}</span>}
            </Form.Group>
            <Form.Group className="mb-3">
                <Form.Label>Message</Form.Label>
                <Form.Control className='formInput' name='message' id='message' as="textarea" rows={3} onChange={handleChange}/>
                {error.message && <span className='bg-secondary p-1 w-100 d-block text-center'>{error.message}</span>}
            </Form.Group>
            <Button variant="Dark" type="submit">Send</Button>
        </Form>
        {loading && <Loader/>}
        {showMSG && <Message message={'The message has been sent 😀'}/>}
        </>
    )
}